@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  font-family: 'Roboto Mono', monospace;
  background-color: #12122A;
  padding: 32px;
  height: 100%;

}

h1,h2,h3 {
  margin: 0;
}

.pull-right {
  float: right;
}

.block {
  display: block;
}



select {
    color: #7D7D95;
    background-color: transparent;
    padding: 15px 25px 15px 25px;
    border: none;
    outline: none;
    font-weight: 700;
    cursor: pointer;
    margin-right: 20px;
    border-radius: 3px;
}

select:hover {
    background-color: #2e2e66;
}

.filters {
    margin-bottom: 40px;
}

.filters label {
    color: #ffffff;
    margin-right: 5px;
}

:root {
    --card-bg-color: #eeeeee;
    --border-radius: 3px;
}

.thumbnail {
    background-color: #eeeeee;
    background-color: var(--card-bg-color);
    padding: 16px;
    background-color: #ffffff;
    border-radius: 3px;
    border-radius: var(--border-radius);
    box-shadow: 0 20px 40px 10px rgba(72, 72, 72, 0.18);
    cursor: pointer;
}

.thumbnail img,.thumbnail video {
    width: 100%;
-webkit-filter: brightness(.8);
        filter: brightness(.8);

}

.thumbnail h3 {
    font-weight: 700;
    font-size: 16px;
    padding: 16px 0 16px 0;
}


.thumbnail img:hover,.thumbnail video:hover {


    -webkit-filter: contrast(1.1) brightness(1.15) saturate(1.1);


            filter: contrast(1.1) brightness(1.15) saturate(1.1);
}


.hidden {
    display: none;
}

.preview li {
    display: inline-block;
}

.preview {
    margin-top: 30px;
    padding-left: 0;
}

.preview li span {
    display: inline-block;
    height: 100px;
    width: 100px;
    border: solid 3px #ffffff;
    cursor: pointer;

}


.preview li span:hover {
    border-color: #f39c12;
}

.preview li.active span {
    border-color: #1abc9c;
}

.stats h3 {
    display: inline;
    line-height: 30px;
}

.stats li {
    display: inline;
    margin-right: 30px;
}

.stats {
    padding: 0;
}

button {
    font-size: 32px;
    cursor: pointer;
    /*padding: 16px 32px 16px 32px;*/
    background-color: transparent;
    color: #ffffff;
    border: none;
    outline: none;
}

button:hover {
    color: #eeeeee;
}

.modal-content {
    background-color: transparent ;
}

.modal-body, .modal-header {
    padding: 0;
}
.modal-header, .modal-footer {
    border: none;
}

.modal-backdrop.show {
    opacity: .9;
}

.modal {
    color: #ffffff;
}

